import { useState, useEffect } from 'react'
import PersonForm from './components/PersonForm'
import Filter from './components/Filter'
import Persons from './components/Persons';
import Notification from './components/Notification';
import phoneService from './services/phonebook'


const App = () => {
  const [persons, setPersons] = useState([])
  const [newName, setNewName] = useState('')
  const [newNumber, setNewNumber] = useState('')
  const [searchName, setSearchName] = useState('')
  const [notification, setNotification] = useState(null)
  let personsToShow = searchName.length >= 1 ? filterPhoneBook() : persons

  useEffect(() => {
    phoneService.getAll()
    .then(initialPersons => {
      setPersons(initialPersons);
    })
  }, [])

  function filterPhoneBook() {
    let search = searchName.toLowerCase()
    return persons.filter(person => person.name.toLowerCase().includes(search))
  }

  const handleSearch = (e) => {
    setSearchName(e.target.value)
  }

  const addName = (e) => {
    e.preventDefault()
    let newPerson = {
      name: newName,
      number: newNumber,
    }
    console.log("new person", newPerson);
    let personFound = persons.find(person => person.name.toLowerCase().includes(newName.toLowerCase()))

    console.log("person found", personFound);

    if (personFound) {
      let result = window.confirm(`Person already in phonebook, replace number with a new one?`)
      if (result) {
        phoneService
        .update(personFound.id, newPerson)
        .then(returnedPerson => {
          setPersons(persons.map(person => person.id === returnedPerson.id ? returnedPerson : person))

          setNotification({type:'success', message: `Updated ${returnedPerson.name}`})
        })
        .catch(err => {
          console.error(err.response);

        })
      }
    } else {
      phoneService
        .create(newPerson)
        .then(returnedPerson => {
          setPersons(persons.concat(returnedPerson))
          setNotification({message: `Updated ${returnedPerson.name}`, type: 'success'})
      }).catch(err => {
        let res = err.response
        console.error(`${res.status} ${res.statusText}: ${res.data.error}`);
        setNotification({message: res.data.error, type:'error'})
      })
    }
    setTimeout(() => {
      setNotification(null)
    }, 3000);
    setNewName('')
    setNewNumber('')
  }

  const deletePerson = (e) => {
    e.preventDefault()
    let id = e.target.dataset.id

    let result = window.confirm('Do you want to delete this contact?')
    if (result) {
      phoneService.deletePerson(id)
      .then(response => {
        if (!response.statusText === 'OK') throw new Error()
        setPersons(persons.filter(person => person.id !== +id))
        setNotification({message: `Deleted contact`, type: 'success'})
        setTimeout(() => {
          setNotification(null)
        }, 3000);
      })
      .catch(err => {
        console.error(err.response)
        setNotification({message: `Information for this contact has already been removed from the server`, type: 'error'})
        setTimeout(() => {
          setNotification(null)
        }, 3000);
      })
    }
  }

  return (
    <div>
      <h2>Phonebook</h2>
      <Notification notification={notification}/>
      <Filter searchName={searchName} handleSearch={handleSearch}/>

      <h3>Add a new Contact</h3>
      <PersonForm addName={addName} newName={newName} setNewName={setNewName} newNumber={newNumber} setNewNumber={setNewNumber} />
      <h2>Numbers</h2>
      <Persons persons={personsToShow} deletePerson={deletePerson}/>
    </div>
  )
}


export default App
