import React from 'react'

const Persons = ({persons, deletePerson}) => {
  return (
      <ul>
    {persons.map((person) =>
      <li key={person.name}>
        {person.name} {person.number}
        <button data-id={person.id} onClick={deletePerson}>Delete</button>
      </li>
    )}
  </ul>
  )
}

export default Persons
