import React from 'react';

const Notification = ({notification}) => {
  if (!notification) {
    return null
  }
  let {message, type} = notification
  let style = {
    fontSize: 20,
    borderStyle: 'solid',
    borderRadius: 5,
    padding: 10,
    marginBottom: 10,
  }
  style.color = type === 'error' ? 'red' : 'green'

  return (
    <div style={style}>
      {message}
    </div>
  )
}

export default Notification
